export function getMHDInfo(res,type) {
    if(res==undefined || (type==11 && (res.mhd_set == undefined || res.mhd_map == undefined) ) || (type==17 && (res.e821_set == undefined || res.e821_map == undefined ))){
        console.log('');
        return [[],false,[]];
    }
    else{
        let ifOutputSource = {'0':'','1':''};
        let data = [];
        let dgm ={};
        let mhdList = [];//mhd
        let mhdInfo = {};//
        let canBuild;//mhd
        let mhd_id_list = [];//
        if(type == 11) {
            mhdList = res.mhd_set;//mhd
            mhdInfo = res.mhd_map;//
        }
        else if(type==17){
            mhdList = res.e821_set;//821
            mhdInfo = res.e821_map;//
        }
        if(mhdList.length === 0){
            canBuild = false;
        }
        else{
            canBuild = true;
        }
        if(mhdInfo && !$.isEmptyObject(mhdInfo)){
            for(let key in mhdInfo){
                if(!$.isEmptyObject(mhdInfo[key])){
                    let dgmInfo = mhdInfo[key];
                    let index = 0;
                    for(let num in dgmInfo){
                        if(!$.isEmptyObject(dgmInfo[num])){
                            if(type==11){
                                dgm={
                                    index: index,
                                    operate:0,
                                    mainMhd:key,
                                    mhdSources:mhdList,
                                    if_output:dgmInfo[num].if_output,
                                    if_output_val:ifOutputSource[dgmInfo[num].if_output],
                                    ifOutputSources:Object.values(ifOutputSource),
                                    ifOutputSources_key:Object.keys(ifOutputSource),
                                    mhd_id:dgmInfo[num].mhd_id,
                                    byName:dgmInfo[num].byName,
                                    byId:dgmInfo[num].byId
                                };
                            }
                            else if(type==17){
                                dgm={
                                    operate:0,
                                    MS_id:dgmInfo[num].MS_id,//id
				                    byId:dgmInfo[num].byId,//
				                    byName:dgmInfo[num].byName,//
				                    SNA:dgmInfo[num].SNA,//
                                    SN:dgmInfo[num].SN,//s/n
				                    updateTime:dgmInfo[num].updateTime,//
                                    main821:key,
                                    main821Sources:mhdList,
                                };
                            }
                            index ++;
                            data.push({dgm});
                        }
                    }

                }
            }
        }

        return [data,canBuild,mhdList];
    }
}
