//key  value
export function getArrFromObj(arr) {
    let keys = [];
    let values = [];
    if(arr.length>0){
        arr.forEach(item =>{
            keys.push(item[0]);
            values.push(item[1]);
        })
    }
    return [keys,values];
}
//key  value
export function getIndexOfArr(arr,value) {
    if(value!==null){
        value = String(value);
    }
    let index = arr.indexOf(value);
    return index;
}
